import React from 'react'

import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import Link from 'gatsby-link'


const MenuItem = ({intitule,path}) => (

<Col md="2" className="mui--text-caption mui--pull-right" ><Link to={path} style={{color:"#FFFFFF"}} >{intitule}</Link></Col>


)

const Menu = () => (
  <Container fluid={true} >
    <Row className="mui--text-right">
      <MenuItem intitule="TICKETS" path={"/tickets"}/>
      <MenuItem intitule="DOCUMENTATION" path={"/"}/>
    </Row>
  </Container>
)

export default Menu
