import React from 'react'


import Helmet from 'react-helmet'

import Header from './header'
import 'muicss/dist/css/mui.css';
import './style.css'


const Layout = ({ children }) => (
  <div>
    <Helmet
      title="COLLEGES"
      meta={[
        { name: 'description', content: 'Informations collèges' },
        { name: 'keywords', content: 'collège, Sarthe' },
      ]}
    />
    <Header siteTitle="COLLEGES" />
    <div
      style={{
        margin: '0 auto',
        maxWidth: 960,
        padding: '0px 1.0875rem 1.45rem',
        paddingTop: 0,
      }}
    >
      {children}
    </div>
  </div>
)



export default Layout
