import React from 'react'
import Link from 'gatsby-link'
import { StaticQuery, graphql } from "gatsby"
import ClgSearch from './clgsearch'
import Menu from './menu'
import Appbar from 'muicss/lib/react/appbar';
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col'


const Header = ({ siteTitle }) => (
<StaticQuery
  query = {graphql`
    query {
      currentBuildDate {
        currentDate
      }
    }
  `}

  render={data =>(
  <Appbar color="#047b8d">
  <div className="mui--pull-right mui--text-light"><small>MAJ: {data.currentBuildDate.currentDate} </small></div>
    <Container fluid={true} >
    <Row style={{marginTop:'15px'}}   >
     <Col md="4" >
       <div className="mui--text-title">
         <Link to="/"  style={{color: 'white',textDecoration: 'none'}} >{siteTitle}</Link>
         </div>
    </Col>
    <Col md="8"><div className="mui--text-subhead"><ClgSearch /></div></Col>
    </Row>
  <Menu/>
    </Container>


  </Appbar>
)}
/>)

export default Header
