var ids = ["0721364S", "0720015A", "0721363R", "0720989J", "0720905T", "0721043T", "0720906U", "0720070K", "0720067G", "0721042S", "0720001K", "0720023J", "0720847E", "0720069J", "0720040C", "0720799C", "0721093X", "0720798B", "0720062B", "0720986F", "0721263G", "0720024K", "0720007S", "0721090U", "0721226S", "0720902P", "0720903R", "0721225R", "0720058X", "0720038A", "0721086P", "0720014Z", "0720046J", "0721262F", "0720800D", "0721281B", "0720002L", "0721304B", "0721477P", "0720004N", "0720797A", "0720081X", "0721224P", "0720068H", "0720019E", "0720904S", "0721044U", "0720988H", "0720987G", "0721483W", "0720011W", "0720053S", "0720051P", "0720043F", "0721089T", "0721607F", "0721365T"];



function compareTwoStrings(first, second) {
	first = first.replace(/\s+/g, '')
	second = second.replace(/\s+/g, '')

	if (!first.length && !second.length) return 1;                   // if both are empty strings
	if (!first.length || !second.length) return 0;                   // if only one is empty string
	if (first === second) return 1;       							 // identical
	if (first.length === 1 && second.length === 1) return 0;         // both are 1-letter strings
	if (first.length < 2 || second.length < 2) return 0;			 // if either is a 1-letter string

	let firstBigrams = new Map();
	for (let i = 0; i < first.length - 1; i++) {
		const bigram = first.substring(i, i + 2);
		const count = firstBigrams.has(bigram)
			? firstBigrams.get(bigram) + 1
			: 1;

		firstBigrams.set(bigram, count);
	};
	let intersectionSize = 0;
	for (let i = 0; i < second.length - 1; i++) {
		const bigram = second.substring(i, i + 2);
		const count = firstBigrams.has(bigram)
			? firstBigrams.get(bigram)
			: 0;

		if (count > 0) {
			firstBigrams.set(bigram, count - 1);
			intersectionSize++;
		}
	}
	return (2.0 * intersectionSize) / (first.length + second.length - 2);
}

class ClgSearch extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      search: "",
      linkid: "",
      find:""
    };
    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  handleSubmit(event) {
    event.preventDefault();
    event.stopPropagation();
    console.log("SUBMIT")
    this.setState({
      search: "",
      linkid:"",
      find: ""
    });
    if(this.state.linkid!==""){
    push("/"+this.state.linkid)
  }
  }

  handleChange(event) {


    var index = 0
    var indexvalue = 0
    for (var i = 0; i < labels.length; i++) {
      var test=compareTwoStrings(event.target.value.toUpperCase(),labels[i])

      if ( test > indexvalue) {
        index = i;
        indexvalue = test
      }
    }
    if (indexvalue !== 0) {
      this.setState({
        search: event.target.value,
        linkid:ids[index],
        find: labels[index]
      });
    } else {
      this.setState({
        search: event.target.value,
        linkid:"",
        find: ""
      });
    }
  }

  render() {
    let showResult
    if(this.state.find===""){
      showResult=<FaSearch />
    //  showResult=<MaterialIcon icon="search" size="tiny" color='#FFFFFF'/>
    }else{
      showResult=<div><span className="mui--text-Subhead" >{this.state.find}</span></div>
    }

    return (
      <div>
      <div className="mui--pull-left">
       <form onSubmit = {this.handleSubmit}><input value = {this.state.search} onChange = {this.handleChange} style={{border:'0px'}} autoComplete="off" autoCorrect="off" autoCapitalize="off" spellCheck="false"/></form>
       </div>
       <div className="mui--pull-left">
       {showResult}
      </div>
      </div>
    )
  }

}

export default ClgSearch
